html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  line-height: 1.5;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.content {
  width: 90%;
  max-width: 120rem;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 10rem;
  transition: 0.5s;
}
@media (max-width: 992px) {
  header {
    padding: 2rem 5rem;
  }
  header .menu-toogle {
    position: relative;
    background-image: url(../../img/menu.png);
    width: 4rem;
    height: 4rem;
    background-size: 3rem;
    background-repeat: no-repeat;
    cursor: pointer;
    background-position: center;
  }
  header .active {
    background-image: url(../../img/close.png);
    background-size: 2.5rem;
    background-repeat: no-repeat;
    background-position: center;
  }
}
header .logo {
  font-size: 4rem;
  color: #fff;
  font-weight: 700;
}
header .logo span {
  color: #ff0157;
}
@media (max-width: 992px) {
  header nav {
    display: none;
  }
  header nav.active {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 7.6rem;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
  }
  header nav.active a {
    font-size: 2.5rem;
    color: #111;
    font-weight: 300;
    margin: 1rem 0;
  }
}
header nav a {
  color: #fff;
  margin-left: 2rem;
}
header nav a:hover {
  color: #ff0157;
}

header.sticky {
  background-color: #fff;
  box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.5);
  padding: 2rem 10rem;
}
@media (max-width: 992px) {
  header.sticky {
    padding: 1.5rem 5rem;
  }
  header.sticky .menu-toogle {
    filter: invert(1);
  }
}
header.sticky .logo {
  color: #111;
}
header.sticky a {
  color: #111;
}
header.sticky a:hover {
  color: #ff0157;
}

.home {
  background-image: url(../../img/bg.jpg);
  height: 100vh;
  min-height: 60rem;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.homeBox {
  width: 95%;
  max-width: 90rem;
  text-align: center;
}
.homeBox h2 {
  color: #fff;
  font-size: 8rem;
  margin-bottom: 1.5rem;
}
@media (max-width: 992px) {
  .homeBox h2 {
    font-size: 6rem;
  }
}
.homeBox p {
  color: #fff;
}
.homeBox .btn {
  color: #fff;
  background-color: #ff0157;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1rem 3rem;
  margin-top: 2rem;
  letter-spacing: 0.2rem;
  transition: 0.5s;
}
.homeBox .btn:hover {
  letter-spacing: 0.6rem;
}

.about {
  display: flex;
  justify-content: center;
}
.about .content {
  padding: 10rem;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .about .content {
    flex-direction: column;
    padding: 5rem 0;
  }
}
.about .content .column50 {
  width: 48%;
}
@media (max-width: 992px) {
  .about .content .column50 {
    width: 100%;
  }
}

.column50 .title {
  color: #111;
  font-size: 4rem;
  font-weight: 300;
}
.column50 .title span {
  color: #ff0157;
  font-weight: 700;
  font-size: 5rem;
}
.column50 .imgBox {
  height: 100%;
}
@media (max-width: 992px) {
  .column50 .imgBox {
    margin-top: 2rem;
  }
}
.column50 .imgBox img {
  height: 100%;
}

.menu {
  padding: 10rem 0;
}
.menu .titleBox {
  text-align: center;
}
.menu .titleBox .title {
  color: #111;
  font-size: 4rem;
  font-weight: 300;
}
.menu .titleBox .title span {
  color: #ff0157;
  font-weight: 700;
  font-size: 5rem;
}

.menu .content {
  margin: 4rem auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.menu .content .box {
  width: 30%;
  box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.5);
  margin: 1rem;
  padding: 3rem;
}
@media (max-width: 992px) {
  .menu .content .box {
    width: 45%;
  }
}
@media (max-width: 480px) {
  .menu .content .box {
    width: 100%;
  }
}
.menu .content .box h3 {
  font-weight: 400;
  padding-top: 0.5rem;
}
.menu .btn {
  color: #fff;
  background-color: #ff0157;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1rem 3rem;
  margin-top: 2rem;
  letter-spacing: 0.2rem;
  transition: 0.5s;
}
.menu .btn:hover {
  letter-spacing: 0.6rem;
}

.experts {
  padding: 10rem 0;
}
.experts .titleBox {
  text-align: center;
}
.experts .titleBox .title {
  color: #111;
  font-size: 4rem;
  font-weight: 300;
}
.experts .titleBox .title span {
  color: #ff0157;
  font-weight: 700;
  font-size: 5rem;
}

.experts .content {
  margin: 4rem auto 0 auto;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .experts .content {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.experts .content .box {
  width: 23%;
}
@media (max-width: 992px) {
  .experts .content .box {
    width: 45%;
    margin: 1rem;
  }
}
@media (max-width: 480px) {
  .experts .content .box {
    width: 100%;
  }
  .experts .content .box .text {
    text-align: center;
  }
}
.experts .content .box h3 {
  font-weight: 400;
  margin-top: 1.5rem;
}

.testimonials {
  background-image: url(../../img/bg2.jpg);
  padding: 10rem 0;
}
.testimonials .titleBox {
  text-align: center;
}
.testimonials .titleBox .title {
  color: #111;
  font-size: 4rem;
  font-weight: 300;
  color: #fff;
}
.testimonials .titleBox .title span {
  color: #ff0157;
  font-weight: 700;
  font-size: 5rem;
}
.testimonials .titleBox p {
  color: #fff;
}

.testimonials .content {
  margin: 4rem auto 0 auto;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .testimonials .content {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.testimonials .content .box {
  width: 30%;
  background-color: #fff;
  padding: 5rem;
}
@media (max-width: 992px) {
  .testimonials .content .box {
    width: 45%;
    margin: 1rem;
  }
}
@media (max-width: 480px) {
  .testimonials .content .box {
    width: 100%;
  }
}
.testimonials .content .box .imgBox {
  text-align: center;
  margin-bottom: 2rem;
}
.testimonials .content .box .imgBox img {
  border-radius: 50%;
  width: 10rem;
}
.testimonials .content .box .text {
  text-align: center;
}
.testimonials .content .box .text h3 {
  color: #ff0157;
  margin-top: 2rem;
}
.testimonials .content .box .text p {
  font-style: italic;
}

.contact {
  background-image: url(../../img/bg3.jpg);
  background-size: cover;
  background-position: center;
  padding: 10rem 0;
}
.contact .titleBox {
  text-align: center;
}
.contact .titleBox .title {
  color: #111;
  font-size: 4rem;
  font-weight: 300;
}
.contact .titleBox .title span {
  color: #ff0157;
  font-weight: 700;
  font-size: 5rem;
}

.content {
  margin: 4rem auto 0 auto;
  display: flex;
  justify-content: left;
}
@media (max-width: 480px) {
  .content {
    justify-content: center;
  }
}
.content .contactForm {
  box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.5);
  padding: 8rem 5rem;
}
@media (max-width: 480px) {
  .content .contactForm {
    padding: 5rem 1rem;
  }
}
.content .contactForm h3 {
  margin: 2rem;
  font-weight: 500;
}
.content .contactForm .inputBox {
  margin: 2rem;
}
.content .contactForm .inputBox input,
.content .contactForm .inputBox textarea {
  padding: 1rem;
  max-width: 100%;
  min-width: 100%;
  resize: none;
}
.content .contactForm .inputBox input[type=submit] {
  color: #fff;
  background-color: #ff0157;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1rem 3rem;
  margin-top: 2rem;
  letter-spacing: 0.2rem;
  transition: 0.5s;
  border: none;
  cursor: pointer;
}
.content .contactForm .inputBox input[type=submit]:hover {
  letter-spacing: 0.6rem;
}

.copyrigth {
  padding: 0.8rem 4rem;
  border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
  text-align: center;
}
.copyrigth span {
  color: #ff0157;
  font-weight: 700;
}